<template>
  <InputTextBase
    v-bind="props"
    :type="type"
    iconName="show"
    @iconMousedown="type = 'text'"
    @iconMouseup="type = 'password'"
    @update:modelValue="onInput"
  />
</template>

<script setup lang="ts">
import { bool, string } from "vue-types";
import InputTextBase from "@/shared/ui_kit/form/InputText/InputTextBase.vue";
import { ref } from "vue";

const props = defineProps({
  placeholder: string().def(""),
  modelValue: string().def(""),
  disabled: bool().def(false),
  error: string(),
  required: bool().def(false),
  label: string().def(""),
});

const emit = defineEmits<{
  (event: "update:modelValue", value: typeof props.modelValue): void;
}>();

const type = ref<"text" | "password">("password");
const onInput = (value: typeof props.modelValue) => {
  emit("update:modelValue", value);
};
</script>
